import PropTypes from 'prop-types'

import styles from './index.scss'

const H1 = ({ children, className }) => {
  return (
    <h1 className={`${styles.headerOne} ${className}`}>
      {children}
    </h1>
  )
}

H1.defaultProps = {
  className: ''
}

H1.propTypes = {
  children: PropTypes.string.isRequired,
  className: PropTypes.string
}

export {
  H1
}